.Gallery {
    margin: 1em 0;
    display: flex;
    height: 30em;
    max-height: 30em;
    box-shadow: 0px 0px 1em purple;
}

.ArrowButton {
    background-color: #f294ff66;
    font-size: 2em;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 10%;
}

.GalleryScreen {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.GalleryWrapper {
    display: grid;
}

.GallerySelectCat {
    margin: 1em 0;
    width:100%;
    display: flex;
    justify-content: space-around;
}

.fillButton {
    width: 20%;
    height: 10vh;
    border: none;
    transition: 0.16s;
}

.fillButton:hover{
    box-shadow: 0px 0px 2em;
}

.fillButton:active{
    box-shadow: 0px 0px 2em 0.3em;
}